<template>

    <div class="page">
        <div class="search">
        
        <el-button type="primary"  @click="add" >添加</el-button>
    </div>
        <el-table :data="tableData" style="width: 100%;">
        <el-table-column type="index" label="序号" ></el-table-column>
        <el-table-column prop="value" label="学校名称" ></el-table-column>
        <el-table-column label="操作" >
            <template slot-scope="scope">
                <el-button size="mini"  type="success" plain @click="handleEdit(scope.$index, scope.row)">修改</el-button>
                <!-- <el-button size="mini"  type="danger" plain @click="handleDel(scope.$index, scope.row)">删除</el-button> -->
            </template>
        </el-table-column>
    </el-table>
    <el-dialog :show-close="false" :close-on-click-modal="false" :title="flag == 1 ?'添加学校':'修改学校'" :visible.sync="dialogVisible" width="50%" center>
         <el-form  label-width="100px"  :model="form"  ref="form" :rules="rules">
            <el-form-item label="学校名称" prop="value" >
                <el-input v-model="form.value"></el-input>
            </el-form-item>
         </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel('form')">取 消</el-button>
            <el-button type="primary" @click="save('form')" :loading="loading">确 定</el-button>
        </span>
    </el-dialog>
    <Pagination v-on:refreshCurrentChange="refreshCurrentChange" v-on:refreshSizeChange="refreshSizeChange" :count="count">
    </Pagination>
    </div>
</template>
<script>
import Pagination from '@/components/element/Pagination'
import API from '@/components/common/Api'

export default {
    components: { Pagination },
    data(){
        return{
            pn:1,
            ps:10,
            count:0,
            flag:'',
            dialogVisible:false,
            tableData: [],
            imageUrl:'',
            loading:false,
            form:{
                value:'',//				               
            },
            rules: {
             value: [
              { required: true, message: '请输入学校名称', trigger: 'blur' },
              { pattern: /^[\u4e00-\u9fa5]+$/, message: '只能输入中文' },
              { min:1,max:30,message:'长度在 1 ~ 30 个字符' ,trigger: 'blur'},
            ],
            
          },
        }
    },
    methods:{
        handleEdit(index,row){
            this.dialogVisible = true;
            this.flag = 2;
            this.form = row;
        },
        save(formName){
            this.$refs[formName].validate((valid) => {
            if (valid) {
                this.loading = true;
                API.School(this.form).then(result=> {
                    this.loading = false;
                    if(result.data == 'error'){
                        this.dialogVisible = true;
                    }else{
                        this.dialogVisible = false;
                        this.$refs[formName].resetFields();
                        this.getList();
                    }
                });
            } else {
                console.log('error submit!!');
                return false;
            }
            });
        },
        add(){
            this.dialogVisible = true;
            this.flag = 1;
            this.value = '';
        },
        cancel(formName){
            this.dialogVisible = false;
            this.$refs[formName].resetFields();
        },
        handleDel(index,row){
            this.$confirm("此操作将永久删除, 是否继续？", "提示", {
                type: "warning",
                center: true
            }).then(_ => {
                let data = {
                    id: row.id
                };
                API.delDisabled(data).then(result => {
                    this.getList();
                });
            }).catch(_ => {});
        },
        refreshCurrentChange(val){
          this.pn = val;
          this.getList();
        },
        refreshSizeChange(val){             
            this.pn = 1;
            this.ps = val;
            this.getList();
        },
        getList(){
            let data = {
                pn:this.pn,
                ps:this.ps,
            };
            API.getSchool(data).then(result=> {
              this.tableData = result.data.data.list;
              this.count = result.data.data.count;
            });
        },
    },
    created(){
        this.getList();
    }
}
</script>





