<template>
  <div class="ftr">{{copy}}</div>
</template>

<script>
export default {
  name:'Footer',
  data(){
    return{
      copy:'',
    }
  },
  mounted(){
    this.copy = '@2018 changsha All Rights Reserved';
    // console.log(this.copy);
  }
  
}
</script>

<style scoped>
  .ftr{
    width:100%;
    position: fixed;
    height: 20px;
    bottom: 0;
    display:-webkit-flex; /* Safari */
    display:flex;
    flex-direction:row;
    justify-content:center;
    align-items:center;
    overflow:hidden;
    background-color:rgb(66,66,66);
    color:silver;
    font-style:italic;font-size:13px;
  }
</style>


